import { RestServiceProvider } from './../../providers/rest-service/rest-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
  providers: [RestServiceProvider]
})

export class LoginPage {
  public email: any;
  public password: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public restService: RestServiceProvider) {
    
  }

  loginApp(){
    this.restService.post("user/login",
      {email: this.email, password: this.password, type: 'NORMAL'}
    )
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

}



